
import XHeader from 'vux/src/components/x-header/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {httpUrl} from '../../http_url';
import myDrawer from './Left.vue';

export default {
directives: {
TransferDom
},
data () {
return {
menus: {
menu1: '退出登录'
},
phone:'',
showMenus: false
}
},
created(){
this.getData();
},
components: {
XHeader,
Drawer,
PopupHeader,
Actionsheet,
myDrawer
},

methods:{
getData(){
const _this = this;
this.$axios({
method: 'post',
url:httpUrl.homePage,
}).then((res)=>{

if(res.data.errCode==200){
this.phone = res.data.retData.phone;
this.msg_type = res.data.retData.msg_type;
this.surplus_amount = res.data.retData.surplus_amount;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
LoginOut(){
this.$axios({
method: 'post',
url:httpUrl.loginOut,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{

if(res.data.errCode==200){
this.$router.push('/quick_login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}

}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},

showDrawer() {
this.$store.dispatch('changeDrawer').then(() => {

})
}
}
}



